body, html {
  width: 100vw;
  height: 100vh;
  background-color: black;
  overflow-x: hidden;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* --------------------- Navbar -----------------------------*/

.Navbar {
  background-color: black;
  color: white;
  display: flex;
  justify-content: space-between;
  font-family: 'Lato', sans-serif;
}

.Navbar .leftSide {
  margin: 15px;
  cursor: pointer;
}

.Navbar .leftSide a {
  text-decoration: none;
  font-size: 1.5rem;
  color: inherit;
  font-weight: 700;
}

.Navbar .rightSide {
  display: flex;
  align-items: center;
}

.Navbar .rightSide a {
  text-decoration: none;
  font-size: 1rem;
  color: inherit;
  font-weight: 700;
  margin-right: 60px;
  cursor: pointer;
}

/* --------------------- Hero Section -----------------------------*/

.hero-image {
  /* Use "linear-gradient" to add a darken background effect to the image (photographer.jpg). This will make the text easier to read */
  background-image: linear-gradient(rgba(0, 0, 0, 0.705), rgba(0, 0, 0, 0.705)), url(./assets/Spoiler\ Zone\ 2.png);

  /* Set a specific height */
  height: 93vh;

  /* Position and center the image to scale nicely on all screens */
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.hero-text {
  font-family: 'Barlow Semi Condensed', sans-serif;
  position: absolute;
  top: 37%;
  left: 100px;
  color: white;
  font-size: 4rem;
  font-weight: 600;
}

.hero-text form input {
  font-family: 'Barlow Semi Condensed', sans-serif;
  background: rgba(255, 255, 255, 0.39);
  border: none;
  outline: none;
  padding: 10px;
  width: 500px;
  color: white;
  font-weight: 600;
  font-size: 1.1rem;
}

.hero-text form input::placeholder {
  color: rgba(255, 255, 255, 0.726);
}

/* --------------------- Footer -----------------------------*/

footer {
  font-family: 'Lato', sans-serif;
  color: white;
  padding: 15px;
  text-align: center;
}

/* --------------------- About -----------------------------*/

.About {
  height: 93vh;
  width: 100vw;
  display: grid;
  grid-template-columns: 45vw 55vw;
  color: white;
}

.About .rightSide {
  height: 100%;
  width: 100%;
  justify-self: center;
  align-self: center;
}

.About .rightSide img {
  max-width:100%;
  height:auto;
  padding: 100px;
  mask-image: linear-gradient(rgba(0, 0, 0, 0.705), rgba(0, 0, 0, 0.705));
}

.About .leftSide {
  margin-left: 30px;
}

.About .leftSide h1 {
  font-family: 'Barlow Semi Condensed', sans-serif;
  font-size: 3rem;
  margin-top: 50px;
  margin-bottom: 30px;
  font-weight: 600;
}

.About .leftSide p {
  font-family: 'Lato', sans-serif;
  font-weight: 700;
  font-size: 1.5rem;
  text-align: justify;
}

/* --------------------- Catalogo -----------------------------*/

.Catalogo {
  margin-left: 30px;
  height: 93vh;
  width: 100vw;
  color: white;
}

.Catalogo h1 {
  font-family: 'Barlow Semi Condensed', sans-serif;
  font-size: 3rem;
  margin-top: 50px;
  margin-bottom: 30px;
}

.Catalogo p {
  font-family: 'Lato', sans-serif;
  font-weight: 700;
  font-size: 1.5rem;
  text-align: justify;
}

.Catalogo a {
  font-family: 'Lato', sans-serif;
  text-decoration: none;
  font-size: 1.5rem;
  color: inherit;
  font-weight: 700;
}

/* --------------------- Filme -----------------------------*/

.Filme img {
  max-width:100%;
  height:auto;
}

.Filme {
  background-color: white;
  padding-bottom: 50px;
}

.Filme h1 {
  font-family: 'Barlow Semi Condensed', sans-serif;
  text-align: center;
  font-size: 3rem;
  margin-top: 30px;
  margin-bottom: 90px;
  font-weight: 600;
}

.Filme p {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-size: 1.5rem;
  text-align: justify;
  margin: 0 100px 50px;
}

.Filme p b {
  font-family: 'Lato', sans-serif;
  font-weight: 700;
  font-size: 1.5rem;
  text-align: justify;
}